<template>
  <div>
    <form @submit.prevent="handleSubmit" class="contact-form">
        <transition name="slide-fade">
            <first-form v-show="forms[0].show" @showForm="showForm"/>
        </transition>
        <transition name="slide-fade" >
            <second-form v-show="forms[1].show" @showForm="showForm" />
        </transition>
        <transition name="slide-fade" >
            <third-form v-show="forms[2].show"  @submit="handleSubmit" @showForm="showForm"/>
        </transition>
    </form>
  </div>
</template>

<script>
import firstForm from './firstForm.vue'
import secondForm from './secondForm.vue'
import thirdForm from './thirdForm.vue'

export default {
  name: 'App',
  components: {
    firstForm,
    secondForm,
    thirdForm
  },
  data(){
      return {
          forms: [
          {show: true},
          {show: false},
          {show: false}
          ]
      }
  },
  methods: {
      showForm(curForm, nextForm) {
          console.log(curForm,nextForm)
          this.forms[curForm].show = false,
          setTimeout(()=> {
              this.forms[nextForm].show = true
          }, 500)
          
      },
      showSecondForm(){
        this.show1 = false;
        setTimeout(()=> {
            this.show2 = true
        }, 500) 
      },
      showThirdForm(){
        this.show2 = false;
        setTimeout(()=> {
            this.show3 = true
        }, 500) 
      },
      handleSubmit() {
          console.log('submitted')
      }
  }
  
}
</script>

<style>
.slide-fade-enter-active {
  transition: all .2s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active до версии 2.1.8 */ {
  transform: translateX(-10px);
  opacity: 0;
}

</style>
