<template>
  <div class="main">
    <div class="contact-form">
          <p class="contact-form__title"> Заполните информацию о себе </p>
          <input 
            type="text"
            v-model.trim="$v.form.surName.$model" 
            id="surName" 
            placeholder="Фамилия" 
            :class="{ 'form-group--error': $v.form.surName.$error }" 
            class="contact-form__element"
          />
          <!-- <div class="error" v-if="!$v.form.surName.required && $v.form.surName.$dirty">*Это поле обязательно</div>
          <div class="error" v-if="!$v.form.surName.alpha && $v.form.surName.$dirty">*Некорректное фамилия</div> -->

          <input 
            type="text" 
            v-model="$v.form.name.$model" 
            id="name" 
            placeholder="Имя" 
            :class="{ 'form-group--error': $v.form.name.$error }" 
            class="contact-form__element"
          />
          <!-- <div class="error" v-if="!$v.form.name.required && $v.form.name.$dirty">*Это поле обязательно</div>
          <div class="error" v-if="!$v.form.name.alpha && $v.form.name.$dirty">*Некорректное имя</div> -->

          <input 
            type="text" 
            v-model="form.fatherName" 
            id="fatherName" 
            placeholder="Отчество"  
            class="contact-form__element"
          />

          <input 
            type="date" 
            v-model="form.birthday" 
            id="birthday" 
            :class="{ 'form-group--error': $v.form.birthday.$error }" 
            placeholder="Дата рождения" 
            class="contact-form__element"
          />
          
          <!-- <div class="error" v-if="!$v.form.birthday.required && $v.form.birthday.$dirty">*Это поле обязательно</div> -->
            <input 
              type="text"
              v-model="form.number" 
              id="number"
              @input="acceptNumber"
              placeholder="Номер телефона" 
              class="contact-form__element"
              :class="{ 'form-group--error': $v.form.number.$error }" 
            />

          <!-- <div class="error" v-if="!$v.form.number.numValidator && $v.form.number.$dirty">*Некорректный номер телефона</div> -->
          

          <select name="sex" id="sex" v-model="form.sex" class="contact-form__element">
            <option selected value="" disabled>Пол</option>
            <option value="male">Мужчина</option>
            <option value="female">Женщина</option>
          </select>

          <select name="doctor" id="doctor" v-model="form.doctor" class="contact-form__element">
            <option selected value="" disabled>Врач</option>
            <option value="Ivanov">Иванов</option>
            <option value="Zaharov">Захаров</option>
            <option value="Chernisheva">Чернышева</option>
          </select>

          <div class="contact-form__element">
            <input type="checkbox" id="notification" v-model="form.notification"> <label class="not-text" for="notification"> Не отправлять смс</label> 
          </div>
          <div class="next-form" @click="showSecondForm"><img src="../assets/right.png"/> 
        </div>
        
      </div>
  </div>
</template>

<script>
import { numValidator } from '../utils/validator'
import { required, alpha} from 'vuelidate/lib/validators'
export default {
  data() {
    return {
      form: {
        surName: "",
        name: "",
        fatherName: "",
        birthday: "",
        number: "",
        sex: "",
        clientGroup: [],
        doctor: "",
        notification: false
      },
    }
  },
  methods: {
    showSecondForm() {
      this.$v.$touch()
      if (this.$v.$invalid) {
        return
      } else {
        this.$emit('showForm',0,1)
      }
    },
    acceptNumber() {
      let x = this.form.number.replace(/\D/g, '').match(/(\d)(\d{0,3})(\d{0,3})(\d{0,4})/);
      this.form.number = !x[2] ? (x[1] == 7 || x[1] == 8 ? '+7' : '+7' + x[1]) : (x[1] == 7 || x[1] == 8 ? '+7' : '+7' + x[1]) + ' '  + x[2]  + (x[3] ? ' ' + x[3] : '') + (x[4] ? ' ' + x[4]: '');
    }
  },
  validations: {
    form: {
      surName: {
      required,
      alpha
    },
      name: {
        required,
        alpha
      },
      birthday: {
        required
      },
      number: {
        numValidator,
      },
    },
    
  }
}
</script>

