<template>
  <div class="main">
      <div class="contact-form">
          <p class="contact-form__title"> Адрес </p>
          <input 
            type="text"
            id="index" 
            placeholder="Индекс" 
            class="contact-form__element"
            v-model="form.index"
          >

          <input 
            type="text" 
            v-model="form.country" 
            id="country" 
            placeholder="Страна" 
            class="contact-form__element"
          >

          <input 
            type="text" 
            v-model="form.region" 
            id="region" 
            placeholder="Область"  
            class="contact-form__element"
          >

          <input 
            type="text" 
            v-model="form.city" 
            id="city" 
            :class="{ 'form-group--error': $v.form.city.$error }" 
            placeholder="Город" 
            class="contact-form__element"
          >
          <!-- <div class="error" v-if="!$v.form.city.required && $v.form.city.$dirty">*Это поле обязательно</div> -->

        <input 
            type="text" 
            v-model="form.street" 
            id="street" 
            placeholder="Улица" 
            class="contact-form__element"
          >
        <input 
            type="text" 
            v-model="form.house" 
            id="number" 
            placeholder="Дом" 
            class="contact-form__element"
          >
          
      </div>
      <div class="next-form" @click="showForm(2)"><img src="../assets/right.png"/></div>
      <div class="prev-form" @click="showForm(0)"><img src="../assets/left.png"/></div>
    </div>
</template>

<script>
import { required} from 'vuelidate/lib/validators'
export default {
  data() {
    return {
      form: {
        region: "",
        city: "",
        street: "",
        house: "",
        index: "",
        country: "",
      }
    }
  },
  methods: {
    showForm(val) {
      if (val == 0){
        this.$emit('showForm',1,0)
      }
      else if(val == 2) {
        console.log('submit!')
        this.$v.$touch()
        if (this.$v.$invalid) {
          console.log('error')
        } else {
          this.$emit('showForm',1,2)
        }
      }
    }
  },
  validations: {
    form: {
      city: {
        required
      }
    },
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
input {
  outline: 0;
  border-width: 0 0 2px;
  border-color: grey;
  
}
input:focus {
  border-color: rgb(63, 62, 62);
}
.contact-form {
  margin: auto auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 15px;
  width: 95%;
  border-radius: 10px;
  background-color: white;
  &__title {
    font-size: 1.5rem;
    padding-bottom: .5rem;
  }
  &__element {
    padding: 10px;
    margin: 10px;
    width: 70%;
  }
  &__btn {
    width: 50%;
    margin: 10px auto;
    padding: 10px;
    cursor: pointer;
  }
}
.main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.error {
  color: red;
  font-size: 0.75rem;
  align-self: flex-start;
  margin-left: 10px;
}
.form-group--error{
  outline: 0;
  border-width: 0 0 2px;
  border-color: red;
}
.not-text {
  padding-left: 5px;
}
.next-form {
  top: 45vh;
  right: .1vw;
  position: absolute;
  cursor: pointer;

}
.prev-form {
  top: 45vh;
  left: .1vw;
  position: absolute;
  cursor: pointer;

}
@media screen and (min-width: 480px) {
  .contact-form {
    width: 80%;
  }
  .next-form {
    right: 10vw;
  }
  .prev-form {
    left: 10vw;
  }
}
@media screen and (min-width: 768px) {
  .contact-form {
    width: 50%;
  }
  .next-form {
    right: 25vw;
  }
  .prev-form {
    left: 25vw;
  }
}
@media screen and (min-width: 992px) {
  .contact-form {
    width: 50%;
  }
  .next-form {
    right: 25vw;
  }
  .prev-form {
    left: 25vw;
  }
}
@media screen and (min-width: 1200px) {
  .contact-form {
    width: 35%;
  }
  .next-form {
    right: 33vw;
  }
  .prev-form {
    left: 33vw;
  }
}
</style>
