<template>
  <div class="main">
      <div class="contact-form">
          <p class="contact-form__title"> Паспорт </p>

          <select name="document-type" id="document-type" v-model="form.documentType" :class="{ 'form-group--error': $v.form.documentType.$error }"  class="contact-form__element">
            <option selected value="" disabled>Тип документа</option>
            <option value="passport">Паспорт</option>
            <option value="birthSign">Свидетельство о рождении</option>
            <option value="driveCard">Водительское удостоверение</option>
          </select>

            <!-- <div class="error" v-if="!$v.form.documentType.required && $v.form.documentType.$dirty">Это поле обязательно</div> -->
            
          <input 
            type="text" 
            v-model="form.series" 
            id="series" 
            placeholder="Серия" 
            class="contact-form__element"
          >

          <input 
            type="text" 
            v-model="form.passNumber" 
            id="passNumber" 
            placeholder="Номер"  
            class="contact-form__element"
          >

          <input 
            type="text" 
            v-model="form.givenBy" 
            id="givenBy" 
            placeholder="Кем выдан" 
            class="contact-form__element"
          >

        <input 
            type="text" 
            v-model="form.dateTaken" 
            id="street" 
            :class="{ 'form-group--error': $v.form.dateTaken.$error }" 
            placeholder="Дата выдачи" 
            class="contact-form__element"
          >
        <!-- <div class="error" v-if="!$v.form.dateTaken.required && $v.form.dateTaken.$dirty">Это поле обязательно</div> -->
          <button type="button" class="contact-form__btn" @click="submitForm">Отправить</button>
      </div>
      <div class="prev-form" @click="showForm"><img src="../assets/left.png"/></div>
    </div>
</template>

<script>
import { required} from 'vuelidate/lib/validators'
export default {
  data() {
    return {
      form: {
        series: "",
        givenBy: "",
        dateTaken: "",
        passNumber: "",
        documentType: ""
      },
    }
  },
  methods: {
    submitForm() {
      console.log('submit!')
      this.$v.$touch()
      if (this.$v.$invalid) {
        console.log('error')
      } else {
        this.$emit('submit')
      }
    },
    showForm() {
        this.$emit('showForm',2,1)
      }
    },
  validations: {
    form: {
      documentType: {
        required
      },
      dateTaken: {
        required
      }
    },
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
input {
  outline: 0;
  border-width: 0 0 2px;
  border-color: grey;
  
}
input:focus {
  border-color: rgb(63, 62, 62);
}
.contact-form {
  margin: auto auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 15px;
  width: 95%;
  border-radius: 10px;
  background-color: white;
  &__title {
    font-size: 1.5rem;
    padding-bottom: 1rem;
  }
  &__element {
    padding: 10px;
    margin: 10px;
    width: 70%;
  }
  &__btn {
    width: 50%;
    margin: 10px auto;
    padding: 10px;
    cursor: pointer;
    border: 0;
    border-radius: 10px;
    background-color:#b8c6db;
    font-size: 1.3rem;
    font-weight: bold;
  }
}
.main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.error {
  color: red;
  margin: 0 auto;
  font-size: 0.75rem;
}
.form-group--error{
  outline: 0;
  border-width: 0 0 2px;
  border-color: red;
}

.prev-form {
  top: 45vh;
  left: .1vw;
  position: absolute;
  cursor: pointer;

}

@media screen and (min-width: 480px) {
  .contact-form {
    width: 80%;
    &__btn {
      width: 35%;
    }
  }
  .prev-form {
    left: 10vw;
  }
}
@media screen and (min-width: 768px) {
  .contact-form {
    width: 50%;
    &__btn {
      width: 35%;
    }
  }
  .prev-form {
    left: 25vw;
  }
}
@media screen and (min-width: 992px) {
  .contact-form {
    width: 50%;
    &__btn {
      width: 25%;
    }
  }
  .prev-form {
    left: 25vw;
  }
}
@media screen and (min-width: 1200px) {
  .contact-form {
    width: 35%;
    &__btn {
      width: 30%;
    }
  }
  .prev-form {
    left: 33vw;
  }
}
</style>
